@use 'utils/breakpoints' as *;
@use 'utils/colors' as *;
@use 'utils/typography' as *;
@use 'structures/buttons' as *;

.view-content-recent {

  .item-list {
    padding: 24px 0;

    .views-field {
      padding: 16px;

      &-title {
        flex-basis: 30%;
      }

      &-uid {
        flex-basis: 20%;
        text-align: center;
      }

      &-changed {
        flex-basis: 20%;
        text-align: center;
      }

      &-type {
        flex-basis: 10%;
        text-align: center;
      }
    }

    > ul > li {
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: $gray-lighter;

      &:nth-child(even) {
        background: $gray-light;
      }
    }
  }
}
